import React,{PureComponent} from 'react';
import MyHead from '../../components/MyHead/index'

export default class Index extends PureComponent {
    constructor (props) {
        super(props)
        this.state = {
            index:0
        }
    }

    render () {
        return <>
            <MyHead index={this.state.index}/>
            <div onClick={()=>{this.setState({index:this.state.index+1})}}>AAA</div>
        </>
    }
}